import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Building } from './building';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { environment } from '../../environments/environment';
import { AuthenticationService } from '../authentication.service';

@Injectable()
export class BuildingService {
    private apiUrl = environment.APIURL + 'buildings';

    constructor(
        private http: Http,
        private authenticationService: AuthenticationService) { }

    getRequestOptions(): RequestOptions {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('Authorization', 'Bearer ' + this.authenticationService.token);
        let options = new RequestOptions({ headers: headers });
        return options;
    }

    getRows(offset: number, limit: number): Observable<Building[]> {
        return this.http.get(this.apiUrl, this.getRequestOptions())
            .map(this.extractData)
            .catch(this.handleError);
    }

    getRow(id: number): Observable<Building> {
        return this.http.get(this.apiUrl + '/' + id, this.getRequestOptions())
            .map(this.extractData)
            .catch(this.handleError);
    }

    addRow(b: Building): Observable<Building> {
        return this.http.post(this.apiUrl, b, this.getRequestOptions())
            .map(this.extractData)
            .catch(this.handleError);
    }

    updateRow(b: Building): Observable<Building> {
        return this.http.put(this.apiUrl + '/' + b.id, b, this.getRequestOptions())
            .map(this.extractData)
            .catch(this.handleError);
    }

    deleteRow(id: number): Observable<Building> {
        return this.http.delete(this.apiUrl + '/' + id, this.getRequestOptions())
            .map(this.extractData)
            .catch(this.handleError);
    }

    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        // if (error instanceof Response) {
        //     const body = error.json() || '';
        //     const err = body.error || JSON.stringify(body);
        //     errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        // } else {
        errMsg = error.message ? error.message : error.toString();
        // }
        console.log(errMsg);
        return Observable.throw(errMsg);
    }
}



