import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChange } from '@angular/core';

@Component({
  selector: 'app-my-pager',
  templateUrl: './my-pager.component.html',
  styleUrls: ['./my-pager.component.css']
})
export class MyPagerComponent implements OnInit, OnChanges {
  @Input() pageRows: number = 10;
  @Input() totalRows: number = 0;
  @Input() activePage: number = 1;
  @Output() onChange = new EventEmitter<number>();

  pages: number[];
  firstPage: number = 1;
  lastPage: number = 1;

  constructor() {
  }

  ngOnInit() {
  }

  ngOnChanges(changes: any) {
    let tr: number = changes.totalRows.currentValue;
    if (tr) {
      this.setPager();
    }
  }

  totalPages(): number {
    let n1 = Math.floor(this.totalRows / this.pageRows);
    let n2 = this.totalRows % this.pageRows;
    if (n2 > 0) {
      n2 = 1;
    }
    return n1 + n2;
  }

  setPager(): void {
    this.pages = new Array<number>();
    if (this.totalRows > this.pageRows) {
      this.firstPage = Math.floor(this.activePage / 10) + 1;
      this.lastPage = this.firstPage + 9;
      if (this.lastPage > this.totalPages()) {
        this.lastPage = this.totalPages();
      }
      for (let i = this.activePage; i <= this.lastPage; i++) {
        this.pages.push(i);
      }
      console.log(this.pages);
    }
  }

  isActive(page: number): boolean {
    return page === this.activePage;
  }

  showPrevious(): boolean {
    return this.activePage > 10;
  }

  showNext(): boolean {
    return this.lastPage < this.totalPages();
  }

  goToPage(page: number): void {
    if (page > this.lastPage) {
      this.setPager();
    }
    if (page < this.firstPage) {
      this.setPager();
    }
    this.activePage = page;
    this.onChange.emit(page);
  }

}



