import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { BuildingService } from './building.service';
import { Building } from './building';
import 'rxjs/add/operator/switchMap';
import * as swal from 'sweetalert';

@Component({
  selector: 'app-building',
  templateUrl: './building.component.html',
  styleUrls: ['./building.component.css'],
  providers: [BuildingService]
})
export class BuildingComponent implements OnInit {
  loading: boolean = false;
  model: Building = new Building();

  constructor(private buildingService: BuildingService, private route: ActivatedRoute) { }

  toggleSpinner(): void {
    this.loading = !this.loading;
  }

  ngOnInit(): void {
    this.toggleSpinner();
    this.route.params
      .switchMap((params: Params) => this.buildingService.getRow(+params['id']))
      .subscribe(
      row => {
        this.model = row;
        this.toggleSpinner();
      },
      error => {
        this.toggleSpinner();
        swal('Error', error, 'error');
      });
  }

  saveRow() {
    this.toggleSpinner();
    if (this.model.id === 0) {
      this.buildingService.addRow(this.model)
        .subscribe(
        row => {
          this.model = row;
          this.toggleSpinner();
          swal('Good job!', 'You clicked the button!', 'success');
        },
        error => {
          this.toggleSpinner();
          swal('Error', error, 'warning');
        });
    } else {
      this.buildingService.updateRow(this.model)
        .subscribe(
        row => {
          this.model = row;
          this.toggleSpinner();
          swal('Good job!', 'You clicked the button!', 'success');
        },
        error => {
          this.toggleSpinner();
          swal('Error', error, 'warning');
        });
    }
  }
}



