import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { MyNavComponent } from './my-nav/my-nav.component';
import { BuildingsComponent } from './buildings/buildings.component';
import { HomeComponent } from './home/home.component';
import { AddressComponent } from './address/address.component';
import { AppartmentsComponent } from './appartments/appartments.component';
import { BuildingComponent } from './building/building.component';
import { LoadingIndicatorComponent } from './loading-indicator/loading-indicator.component';
import { MyPagerComponent } from './my-pager/my-pager.component';
import { MyLoginComponent } from './my-login/my-login.component';
import { AuthenticationService } from './authentication.service';
import { AuthGuard } from './auth.guard';

@NgModule({
  declarations: [
    AppComponent,
    MyNavComponent,
    BuildingsComponent,
    HomeComponent,
    AddressComponent,
    AppartmentsComponent,
    BuildingComponent,
    LoadingIndicatorComponent,
    MyPagerComponent,
    MyLoginComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot([
        { path: '', redirectTo: 'home', pathMatch: 'full' },
        { path: 'home', component: HomeComponent },
        { path: 'login', component: MyLoginComponent },
        { path: 'buildings', component: BuildingsComponent, canActivate: [AuthGuard] },
        { path: 'buildings/:id', component: BuildingComponent, canActivate: [AuthGuard] },
        { path: '**', redirectTo: 'home' }
    ])
  ],
  providers: [
    AuthGuard,
    AuthenticationService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



